import React from "react";
import imga from "../staticimages/conste.svg";

export default function Experience() {

  const internship = [{internshipname:"Aakansha E-Commerce",position:"Frontend End Web Developer",duration:"3 months"},
  {internshipname:"Creatix Digibar",position:"Web Developer",duration:"6 months"},
  {internshipname:"The Sparks Foundation",position:"Data Science and Business Analytics Intern",duration:"1 month"}]
  return (
    <div
      style={{
        backgroundImage: `url(${imga})`,
        backgroundSize: "cover",
      }}
    >
      <section className="text-gray-600 body-font">
        <div className="container px-5 py-24 mx-auto">{internship.map((element) => (<>
          <div className="flex items-center lg:w-4/5 mx-auto border-b pb-10 mb-10 border-gray-200 sm:flex-row flex-col"> 
          
            <div className="sm:w-32 sm:h-32 h-20 w-20 sm:mr-10 inline-flex items-center justify-center rounded-full bg-indigo-100 text-indigo-500 flex-shrink-0">
              <svg
                fill="none"
                stroke="currentColor"
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                className="sm:w-16 sm:h-16 w-10 h-10"
                viewBox="0 0 24 24"
              >
                <path d="M22 12h-4l-3 9L9 3l-3 9H2"></path>
              </svg>
            </div>
           
            <div className="flex-grow sm:text-left text-center mt-6 sm:mt-0">
               <h2 className="text-gray-100 text-xl title-font font-medium mb-2">
               {element.internshipname}
              </h2>
              <h2 className="text-teal-400 text-md title-font font-medium mb-2">
               Position : {element.position}
              </h2>
              <p className="text-teal-400 text-md title-font font-medium mb-2">
               Duration : {element.duration}
              </p>
            </div> 

          </div></> ))}
        </div>
      </section>
    </div>
  );
}
